<template>
    <div id="UserRegister">
      <p class="AppAllExplain">{{msg}}</p>
      <top-base></top-base>
      <!--  -->
      <div>
        <p id="identity"><span style="color: red;" v-if="user.identity === ''">*</span><span style="color: dodgerblue;">{{user.identity | EnglishTranslate}}</span></p>
        <button @click="user.identity = 'onlineBuyer'" :disabled="$store.state.user.identity !== ''"></button>
        <button @click="user.identity = 'boss'" :disabled="$store.state.user.identity !== ''"></button>
        <button @click="user.identity = 'assistant'" :disabled="!agreeAssistantRegister && !($store.state.user.identity === 'boss')"></button>
        <!-- vs -->
        <span style="color: red;" v-if="mustFilled === 'identity'"></span>
      </div>
      <div>
        <p id="userName"><span style="color: red;" v-if="user.userName === ''">*</span></p>
        <input type="text" v-model="user.userName" placeholder=""><span style="color: red;" v-if="mustFilled === 'userName'"></span>
        <p></p>
        <input type="password" v-model="user.userPassword">
        <p></p>
        <input type="password" v-model="confirmPassword"><span style="color: red;" v-if="!contrastPassword"></span>
        <p></p>
        <input type="number" v-model="user.telephone"><span style="color: red;" v-if="!checkoutTelephone"></span>
        <div><button @click="submitUserMsg"></button></div>
        <p style="color: red;" v-if="mustFilled !== false && mustFilled !== true">{{mustFilled | EnglishTranslate}}</p>
        <span style="color: red;" v-if="!contrastPassword"></span>
      </div>
    </div>
</template>

<script>
import TopBase from '../Base/TopBase'

export default {
  name: 'user-register',
  components: {
    'TopBase': TopBase
  },
  data () {
    return {
      msg: '',
      // -------------------------------------------------------------------- \\
      user: { // 
        userName: '', // 
        userPassword: '', // 
        identity: '', // 
        telephone: '' // 
      },
      // -------------------------------------------------------------------- \\
      confirmPassword: '', // 
      mustFilled: false, // 
      operateUser: {}, // 
      agreeAssistantRegister: false // boss
    }
  },
  created () {
    this.operateUser = this.$jsfn.determineUserLoginSuccess() // 'src/assets/js/jsfn.js'
    if (this.operateUser) {
      console.log('UserRegister')
      // 
      if (this.operateUser.identity === 'boss') {
        // 
        this.agreeAssistantRegister = true
      } else {
        console.log('')
      }
    } else {
      console.log('UserRegister')
    }
  },
  computed: {
    // 
    contrastPassword () {
      if (this.confirmPassword) {
        return this.confirmPassword === this.user.userPassword
      } else {
        return true
      }
    },
    // 
    checkoutTelephone () {
      var reg = new RegExp('^((13[0-9])|(14[5,7])|(15[0-3, 5-9])|(17[0, 3, 5-8])|(18[0-9])|166|198|199|(147))\\d{8}$')
      if (this.user.telephone) {
        return reg.test(this.user.telephone)
      } else {
        return true
      }
    }
  },
  methods: {
    /*
    * 
    * */

    /*
    * 
    * */
    // ----------------------------------------------
    // 
    goAnchor (selector) {
      this.$el.querySelector(selector).scrollIntoView() // querySelectorcssscrollIntoViewtruefalse
    },

    // 
    emptyShow () {
      console.log('')
      if (this.user.identity === '') {
        this.mustFilled = 'identity'
        this.goAnchor('#identity')
      } else if (this.user.userName === '') {
        this.mustFilled = 'userName'
        this.goAnchor('#userName')
      } else {
        this.mustFilled = true
      }
    },
    // --------------------------------------------------
    // 
    submitUserMsg () {
      console.log('')
      this.emptyShow() // 
      if (this.mustFilled === true && this.contrastPassword && this.checkoutTelephone) { // 
        // md5 ------------------------------------------------------------------------------- to-do
        console.log(this.user)
        this.$sendRequest.RTSPost('/rm_users/add_user', {user: this.user, operateUser: this.operateUser}).then(res => {
          console.log(res)
          var status
          if (res.data.code === 0) {
            status = 'success'
            // 
          } else {
            status = 'warning'
          }
          this.$message({
            message: res.data.msg,
            type: status
          })
        })
      }
    }
  }
}
</script>

<style scoped>

</style>



