<template>
  <div id="TopBase">
    <p class="AppAllExplain">{{msg}}</p>
    <div class="topBox">
        <div class="topBody">
          <ul class="leftList">
            <li class="listItem"><i class="iconfont icon-dingwei"></i></li>
          </ul>
          <ul class="rightList">
            <li class="listItem">
              <div class="itemBox">
                <span class="itemBtn" v-if="!$store.state.user.userName" @click="invokingLoginWindow"></span>
                <el-dropdown size="mini" split-button type="primary" v-if="$store.state.user.userName" @click="$router.replace('/User')" @command="userHandleCommand">
                  {{$store.state.user.userName}}
                  <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item></el-dropdown-item>
                    <el-dropdown-item></el-dropdown-item>
                    <el-dropdown-item></el-dropdown-item>
                    <el-dropdown-item></el-dropdown-item>
                    <el-dropdown-item command="loginOut"></el-dropdown-item>
                  </el-dropdown-menu>
                </el-dropdown>
              </div>
            </li>
            <li class="spacer">|</li>
            <li class="listItem">
              <div class="itemBox">
                <router-link class="itemBtn" to="/UserRegister"></router-link>
              </div>
            </li>
            <li class="spacer">|</li>
            <li class="listItem">
              <div class="itemBox">
                <span class="itemBtn" @click="invokingLoginWindow"></span>
              </div>
            </li>
            <li class="spacer">|</li>
            <li class="listItem">
              <div class="itemBox">
                <span class="itemBtn" @click="invokingLoginWindow"></span>
              </div>
            </li>
            <li class="spacer">|</li>
            <li class="listItem">
              <div class="itemBox">
                <router-link class="itemBtn" to="/Home"></router-link>
              </div>
            </li>
          </ul>
        </div>
      </div>
    </div>
</template>

<script>
export default {
  name: 'TopBase',
  data () {
    return {
      msg: ''
    }
  },
  created () {},
  methods: {
    /*
    * 
    * */
    /*
    * 
    * */

    invokingLoginWindow () {
      console.log('')
      this.$windowFn.openLoginWindow('body', {}, '', '')
    },

    // 
    userHandleCommand (command) {
      switch (command) {
        case 'loginOut':
          this.loginOut()
      }
    },

    // 
    loginOut () {
      console.log('')
      this.$sendRequest.RTSGet('/rm_users/user_logout', this.$store.state.user).then(res => {
        console.log(res)
        var status // 
        if (res.data.code === 0) { // 
          status = 'success'
          this.$router.push('/Home') // 
        } else {
          status = 'warning'
          //   cookiessession 
          // Cookiesvuexstoreuser
        }
        this.$store.state.user = {userId: '', userName: '', identity: ''} // vuexstoreuser
        this.$message({
          message: res.data.msg,
          type: status
        })
      })
    }
  }
}
</script>

<style scoped lang="less">
  #TopBase{
    font-size: 12px;
    .topBox{
      border-bottom: 1px solid #ddd;
      background-color: #e3e4e5;
      overflow: hidden;
      .topBody{
        height: 30px;
        line-height: 30px;
        color: #999;
        margin: auto;
        width: 1190px;
        .leftList, .rightList{
          >li{
            display: inline-block;
            float: left;
            margin: 0 6px;
            &.listItem{
              .itemBox{
                .itemBtn{
                  cursor: pointer;
                  color: #999;
                  &:hover{
                    color: red;
                  }
                }
              }
            }
          }

        }
        .leftList{
          .listItem{
            .icon-dingwei{
              color: red;
            }
          }
        }
        .rightList{
          float: right;
        }
      }
    }
  }
</style>



