<template>
    <div id="UserMsg">
      <p class="AppAllExplain">{{msg}}</p>
      <div class="userMsgBox">
        <div style="border: 1px solid #DEDEDE; padding: 20px;">
          <ul style="border-bottom: 1px solid #DEDEDE;">
            <li>
              <span></span>
              <button style="float: right;" @click="updateUserMsg('baseMsg')" v-if="!baseMsgUpdate"></button>
              <button style="float: right;" @click="submitUpdateDate('baseMsg')" v-if="baseMsgUpdate"></button>
            </li>
            <li style="text-align: center;"><span></span><span>{{userMsg.userName}}</span></li>
            <li style="text-align: center;"><span></span><span>{{userMsg.identity}}</span></li>
            <li style="text-align: center;"><span></span><span>{{userMsg.userImage}}</span></li>
            <li style="text-align: center;" v-if="!baseMsgUpdate"><span></span><span>{{userMsg.userSex}}</span></li>
            <li style="text-align: center;" v-if="baseMsgUpdate"><span></span><input type="text" v-model="userNewMsg.userSex"></li>
          </ul>
          <ul style="border-bottom: 1px solid #DEDEDE;">
            <li>
              <span></span>
              <button style="float: right;" @click="updateUserMsg('communicationMsg')" v-if="!communicationMsgUpdate"></button>
              <button style="float: right;" @click="submitUpdateDate('communicationMsg')" v-if="communicationMsgUpdate"></button>
            </li>
            <li style="text-align: center;" v-if="!communicationMsgUpdate"><span></span><span>{{userMsg.telephone}}</span></li>
            <li style="text-align: center;" v-if="communicationMsgUpdate"><span></span><input type="text" v-model="userNewMsg.telephone"></li>
            <li style="text-align: center;"><span></span><span></span></li>
            <li style="text-align: center;">
              <span></span>
              <ul>
                <li v-for="(addressItem, index) in userMsg.addressList" :key="index" v-if="!communicationMsgUpdate"><span>{{addressItem}}</span></li>
                <li v-for="(addressItem, index) in userNewMsg.addressList" :key="index" v-if="communicationMsgUpdate"><span>{{addressItem}}</span></li>
              </ul>
            </li>
          </ul>
          <ul style="border-bottom: 1px solid #DEDEDE;">
            <li>
              <span></span>
              <button style="float: right;" @click="updateUserMsg('adjunctionMsg')" v-if="!adjunctionMsgUpdate"></button>
              <button style="float: right;" @click="submitUpdateDate('adjunctionMsg')" v-if="adjunctionMsgUpdate"></button>
            </li>
            <li style="text-align: center;"><span></span><span>{{userMsg.lastLoginTime}}</span></li>
            <li style="text-align: center;" v-if="!adjunctionMsgUpdate">
              <span></span>
              <el-tag v-for="(assistant, index) in userMsg.myAssistant" :key="index" style="margin: 0 4px;">{{assistant}}</el-tag>
            </li>
            <li style="text-align: center;" v-if="adjunctionMsgUpdate">
              <span></span>
              <el-tag
                v-for="(assistant, index) in userNewMsg.myAssistant"
                :key="index"
                closable
                type=''
                @close="deleteMyAssistant(assistant)"
                style="margin: 0 4px;">
                {{assistant}}
              </el-tag>
            </li>
            <li style="text-align: center;"><span></span><span>{{userMsg.myBoss}}</span></li>
            <li style="text-align: center;" v-if="userMsg.meta && userMsg.meta.createAt"><span></span><span>{{userMsg.meta.createAt}}</span></li>
            <li style="text-align: center;" v-if="userMsg.meta && userMsg.meta.updateAt"><span></span><span>{{userMsg.meta.updateAt}}</span></li>
          </ul>
        </div>
      </div>
    </div>
</template>

<script>
export default {
  name: 'user-msg',
  data () {
    return {
      msg: '',
      operateUser: {}, // 
      searchData: { // 
        _id: '' // _id
      },
      userMsg: {}, // 
      baseMsgUpdate: false, // 
      communicationMsgUpdate: false, // 
      adjunctionMsgUpdate: false, // 
      userNewMsg: { // 
        userSex: '',
        telephone: '',
        addressList: [],
        myAssistant: [],
        deleteAssistant: [] // 
      },
      updateItem: {} // 
    }
  },
  created () {
    var vm = this
    this.operateUser = this.$jsfn.determineUserLoginSuccess() // 'src/assets/js/jsfn.js'
    if (this.operateUser) {
      console.log('')
      vm.searchData._id = vm.operateUser.userId // ,
      vm.getUserIntegrityMsgByUserId() // 
    } else {
      console.log('', vm.operateUser)
      vm.$message({
        type: 'warning',
        message: ''
      })
      // User.vue
    }
  },
  methods: {
    /*
    * 
    * */
    // 
    getUserIntegrityMsgByUserId () {
      console.log('userId')
      this.$sendRequest.RTSPost('/rm_users/users_by_searchData', {searchData: this.searchData}).then(res => {
        console.log(res)
        if (res.data.code === 0) {
          this.userMsg = res.data.data
          this.userNewMsg.userSex = this.userMsg.userSex
          this.userNewMsg.telephone = this.userMsg.telephone
          this.userNewMsg.communicationMsgUpdate = this.userMsg.communicationMsgUpdate
          this.userNewMsg.myAssistant = this.userMsg.myAssistant.concat()
        }
      })
    },
    /*
    * 
    * */
    /* --------------------------- */

    /* ------------------------------- */
    // 
    updateUserMsg (msgLocation) {
      console.log('', msgLocation)
      switch (msgLocation) {
        case 'baseMsg':
          console.log('')
          this.baseMsgUpdate = true // 
          break
        case 'communicationMsg':
          console.log('')
          this.communicationMsgUpdate = true // 
          break
        case 'adjunctionMsg':
          console.log('')
          this.adjunctionMsgUpdate = true // 
          break
      }
    },

    // myAssistant
    deleteMyAssistant (item) {
      console.log('', item)
      var index = this.userNewMsg.myAssistant.indexOf(item)
      if (index !== -1) {
        this.userNewMsg.deleteAssistant.push(item) // 
        this.userNewMsg.myAssistant.splice(index, 1)
        console.log(this.userNewMsg.myAssistant)
      } else {
        console.log('userNewMsg.myAssistant')
      }
    },

    // 
    updateUpdateItem () {
      console.log('')
      this.updateItem = {
        userSex: this.userNewMsg.userSex === this.userMsg.userSex ? '' : this.userNewMsg.userSex,
        telephone: this.userNewMsg.telephone === this.userMsg.telephone ? '' : this.userNewMsg.telephone,
        communicationMsgUpdate: this.userNewMsg.communicationMsgUpdate === this.userMsg.communicationMsgUpdate ? '' : this.userNewMsg.communicationMsgUpdate,
        myAssistant: this.$jsfn.contrastArray(this.userNewMsg.myAssistant, this.userMsg.myAssistant) ? '' : this.userNewMsg.myAssistant,
        deleteAssistant: this.userNewMsg.deleteAssistant === [] ? '' : this.userNewMsg.deleteAssistant
      }
    },

    // 
    submitUpdateDate (msgLocation) {
      this.updateUpdateItem() // 
      // _id_idupdateDataoperateUser
      this.$sendRequest.RTSPost('/rm_users/update_users', {_id: this.userMsg._id, updateData: this.updateItem, operateUser: this.operateUser}).then(res => {
        console.log(res)
        var status // 
        var messageFrom // 
        if (res.data.code === 0) { // 
          status = 'success'
          switch (msgLocation) { // 
            case 'baseMsg':
              this.baseMsgUpdate = false // 
              messageFrom = ''
              break
            case 'communicationMsg':
              this.communicationMsgUpdate = false // 
              messageFrom = ''
              break
            case 'adjunctionMsg':
              this.adjunctionMsgUpdate = false // 
              messageFrom = ''
              break
          }
        } else {
          status = 'warning'
        }
        this.$message({
          message: res.data.msg + '' + messageFrom + '',
          type: status
        })
      })
    }
  }
}
</script>

<style scoped>

</style>



