import Vue from 'vue'
import Router from 'vue-router'
/*
* 
* */
import Home from '@/page/Home'
import AddNewGoods from '@/components/Home/AddNewGoods'
import AddNewPurchaseMsg from '@/components/Home/AddNewPurchaseMsg'
/*
*  
* */
import ListShow from '@/page/ListShow'
import GoodsList from '@/components/ListShow/GoodsList'
import PurchasesList from '@/components/ListShow/PurchasesList'
/*
* 
* */
import User from '@/page/User'
import UserRegister from '@/components/User/UserRegister' // 
import UserPersonalCenter from '@/components/User/UserPersonalCenter' // 
import UserMsg from '@/components/User/UserMsg' // 
import UsersList from '@/components/User/Admin/UsersList' // admin
import ChooseMyBoss from '@/components/User/assistant/ChooseMyBoss' // assistant

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: 'Home'
    },
    // 
    {
      path: '/Home',
      name: 'Home',
      component: Home,
      children: [
        { // 
          path: 'AddNewGoods',
          name: 'AddNewGoods',
          component: AddNewGoods
        },
        { // 
          path: 'AddNewPurchaseMsg',
          name: 'AddNewPurchaseMsg',
          component: AddNewPurchaseMsg
        }
      ]
    },
    // 
    {
      path: '/ListShow',
      name: 'ListShow',
      component: ListShow,
      children: [
        { // 
          path: 'GoodsList',
          name: 'GoodsList',
          component: GoodsList
        },
        { // 
          path: 'PurchasesList',
          name: 'PurchasesList',
          component: PurchasesList
        }
      ]
    },
    // 
    { // '/User'
      path: '/UserRegister',
      name: 'UserRegister',
      component: UserRegister
    },
    { // 
      path: '/User',
      name: 'User',
      component: User,
      children: [
        {
          path: '/User', // /:to="{name: 'User'}"UserPersonalCenter
          redirect: 'UserPersonalCenter'
        },
        { // 
          path: 'UserPersonalCenter',
          name: 'UserPersonalCenter',
          component: UserPersonalCenter
        },
        { // 
          path: 'UserMsg',
          name: 'UserMsg',
          component: UserMsg
        },
        { // admin
          path: 'UsersList',
          name: 'UsersList',
          component: UsersList
        },
        { // assistant
          path: 'ChooseMyBoss',
          name: 'ChooseMyBoss',
          component: ChooseMyBoss
        }
      ]
    }
  ]
})



