<template>
  <div id="FullDisplayText" style="position: absolute; "
       :style="{
          left: left + 'px',
          top: top + 'px',
          width: width + 'px'
       }">
    <p class="AppAllExplain">{{msg}}</p>
    <p>{{fullText}}</p>
  </div>
</template>

<script>
export default {
  name: 'full-display-text',
  data () {
    return {
      msg: '',
      show: false, // 
      data: '', // 
      top: 0, // 
      left: 0, // 
      width: 0 // 
    }
  }
}
</script>

<style scoped>

</style>



