<!--  2019/01/08 -->
<template>
  <div id="AddNewPurchaseMsg">
    <p class="AppAllExplain">{{msg}}</p>
    <!---->
    <h3>{{purchase_id === 'add'? '' : '' + purchase_id + ''}}</h3>
    <div style="text-align: center; position: relative;" v-if="purchase_id === 'add'">
      <p>{{productPurchaseDate}}</p>
      <p></p>
      <input type="text" v-model="productPurchaseSeller" v-on:input="inputFunction('purchaseSeller')">
      <div style="position: relative;" @click="showProductNameList">
        <p id="productName"><span style="color: red;" v-if="!productPurchaseName">*</span></p>
        <input type="text" v-model="productPurchaseName"
               v-on:input="inputFunction('purchaseName')"
               v-on:focus="showProductNameList; mustFilled = 'false'"
        ><span style="color: red;" v-if="mustFilled === 'name'"></span>
        <div v-if="nameMsgHintList.show" style="position: absolute; left: 625px; top: 40px; width: 173px;"> <!--  -->
          <ul style="background: #a71d5d; color: #fff;">
            <li style="border-top: 1px solid slateblue;"
                v-bind:class="{optionListActive: productPurchaseName === item._id || nameMsgHintList.forecastOption === item._id}"
                v-on:mouseenter="nameMsgHintList.forecastOption = item._id"
                v-on:mouseleave="nameMsgHintList.forecastOption = false"
                v-for="(item, index) in msgHintListData"
                :key="index"
                @click="productPurchaseName = item._id"
              ><span style="word-wrap: break-word;">{{item._id}}</span
            ></li> <!-- word-wrap:break-word;  -->
          </ul>
        </div>
      </div>
      <div>
        <p id="productPurchaseSpecificationMsg"><span style="color: red;" v-if="productPurchaseSpecificationMsg.length === 0 || productPurchaseSpecificationMsg[0].productPurchaseId === ''">*</span></p><span style="color: red;" v-if="mustFilled === 'productPurchaseSpecificationMsg'"></span>
        <div v-for="(item, index) in productPurchaseSpecificationMsg" :key="index">
          <span>{{index + 1}}</span>
          <select v-on:focus="acquireProductId" v-model="item.productPurchaseId" v-on:change="boundChargePrice(index, item.productPurchaseId)">
            <!-- 'productPurchaseSpecificationMsg.filter(function(e) {return e.productPurchaseId === item._id}).length !== 0' filterproductPurchaseId ===  -->
            <option
              :value="goods.productId"
              v-for="(goods, goodsIndex) in goodsListDB"
              :key="goodsIndex"
              :disabled="productPurchaseSpecificationMsg.filter(function(e) {return e.productPurchaseId === goods.productId}).length !== 0"
            >{{goods.productId}}</option>
          </select>
          <span></span>
          <input type="number" v-model="item.productPurchaseNum">
          <span></span>
          <input type="number" v-model="item.productPurchaseCost" v-on:input="contrastChargePrice(index, item.productPurchaseId, item.productPurchaseCost)">
          <span style="color: #ec09ec;" v-if="item.priceDiffer && item.priceDiffer !== 0"><span style="color: red;">{{item.priceDiffer}}</span></span>
          <span>{{item.productPurchaseId}}</span>
          <span>{{item.productPurchaseNum * item.productPurchaseCost}}</span>
          <span @click="deleteSpecifications(index)"><i class="iconfont icon-jianshao" style="color: red; margin-left: 10px; cursor: pointer;"></i></span>
        </div>
        <span @click="addSpecifications"><i class="iconfont icon-tianjia" style="font-size: 22px; cursor: pointer;"></i></span>
        <p>{{countTotalPrice(productPurchaseSpecificationMsg).num}}</p>
        <p>{{countTotalPrice(productPurchaseSpecificationMsg).sum}}</p>
      </div>
      <p></p>
      <input type="number" v-model="productPurchaseFreight">
      <p></p>
      <input type="text" v-model="productPurchaseShortage" placeholder="productPurchaseShortage">
      <p style="color: red;"></p>
      <input type="text" style="color: red;" v-model="productPurchaseSpecificAttention">
      <p></p>
      <input type="text" v-model="productPurchaseExplain">
      <div>
        <button @click="submitPurchaseData"></button>
      </div>
    </div>
    <hr>
    <!---->
    <div style="text-align: center; position: relative; float: left;" v-if="purchase_id !== 'add'">
      <p>{{originalPurchaseData.productPurchaseDate}}</p>
      <p></p>
      <input type="text" v-model="originalPurchaseData.productPurchaseSeller" disabled>
      <p></p>
      <input type="text" v-model="originalPurchaseData.productPurchaseName" disabled>
      <div>
        <p></p>
        <div v-for="(item, index) in originalPurchaseData.productPurchaseSpecificationMsg" :key="index">
          <span>{{index + 1}}</span>
          <span>{{item.productPurchaseId}}</span>
          <span></span>
          <input type="number" v-model="item.productPurchaseNum" disabled><br>
          <span></span>
          <input type="number" v-model="item.productPurchaseCost" disabled><br>
          <span style="color: #ec09ec;" v-if="item.priceDiffer && item.priceDiffer !== 0"><span style="color: red;">{{item.priceDiffer}}</span></span>
          <span>{{item.productPurchaseId}}</span>
          <span>{{item.productPurchaseNum * item.productPurchaseCost}}</span>
        </div>
        <p>{{countTotalPrice(originalPurchaseData.productPurchaseSpecificationMsg).num}}</p>
        <p>{{countTotalPrice(originalPurchaseData.productPurchaseSpecificationMsg).sum}}</p>
      </div>
      <p></p>
      <input type="number" v-model="originalPurchaseData.productPurchaseFreight" disabled>
      <p></p>
      <input type="text" v-model="originalPurchaseData.productPurchaseShortage" placeholder="productPurchaseShortage" disabled>
      <p style="color: red;"></p>
      <input type="text" style="color: red;" v-model="originalPurchaseData.productPurchaseSpecificAttention" disabled>
      <p></p>
      <input type="text" v-model="originalPurchaseData.productPurchaseExplain" disabled>
    </div>
    <div style="text-align: center; position: relative;" v-if="purchase_id !== 'add'">
      <p>{{productPurchaseDate}}</p>
      <p></p>
      <input type="text" v-model="productPurchaseSeller" v-on:input="inputFunction('purchaseSeller')">
      <div style="position: relative;" @click="showProductNameList">
        <p id="productName"><span style="color: red;" v-if="!productPurchaseName">*</span></p>
        <input type="text" v-model="productPurchaseName"
               v-on:input="inputFunction('purchaseName')"
               v-on:focus="showProductNameList; mustFilled = 'false'"
        ><span style="color: red;" v-if="mustFilled === 'name'"></span>
        <div v-if="nameMsgHintList.show" style="position: absolute; left: 1032px; top: 40px; width: 173px;"> <!--  -->
          <ul style="background: #a71d5d; color: #fff;">
            <li style="border-top: 1px solid slateblue;"
                v-bind:class="{optionListActive: productPurchaseName === item._id || nameMsgHintList.forecastOption === item._id}"
                v-on:mouseenter="nameMsgHintList.forecastOption = item._id"
                v-on:mouseleave="nameMsgHintList.forecastOption = false"
                v-for="(item, index) in msgHintListData"
                :key="index"
                @click="productPurchaseName = item._id"
            ><span style="word-wrap: break-word;">{{item._id}}</span
            ></li> <!-- word-wrap:break-word;  -->
          </ul>
        </div>
      </div>
      <div>
        <p id="updateProductPurchaseSpecificationMsg"><span style="color: red;" v-if="productPurchaseSpecificationMsg.length === 0 || productPurchaseSpecificationMsg[0].productPurchaseId === ''">*</span></p><span style="color: red;" v-if="mustFilled === 'updateProductPurchaseSpecificationMsg'"></span>
        <div v-for="(item, index) in productPurchaseSpecificationMsg" :key="index">
          <span>{{index + 1}}</span>
          <select v-on:focus="acquireProductId" v-model="item.productPurchaseId" v-on:change="boundChargePrice(index, item.productPurchaseId)">
            <!-- 'productPurchaseSpecificationMsg.filter(function(e) {return e.productPurchaseId === item._id}).length !== 0' filterproductPurchaseId ===  -->
            <option
              :value="goods.productId"
              v-for="(goods, goodsIndex) in goodsListDB"
              :key="goodsIndex"
              :disabled="productPurchaseSpecificationMsg.filter(function(e) {return e.productPurchaseId === goods.productId}).length !== 0"
            >{{goods.productId}}</option>
          </select>
          <span></span>
          <input type="number" v-model="item.productPurchaseNum">
          <span></span>
          <input type="number" v-model="item.productPurchaseCost" v-on:input="contrastChargePrice(index, item.productPurchaseId, item.productPurchaseCost)">
          <span style="color: #ec09ec;" v-if="item.priceDiffer && item.priceDiffer !== 0"><span style="color: red;">{{item.priceDiffer}}</span></span>
          <span>{{item.productPurchaseId}}</span>
          <span>{{item.productPurchaseNum * item.productPurchaseCost}}</span>
          <span @click="deleteSpecifications(index)"><i class="iconfont icon-jianshao" style="color: red; margin-left: 10px; cursor: pointer;"></i></span>
        </div>
        <span @click="addSpecifications"><i class="iconfont icon-tianjia" style="font-size: 22px; cursor: pointer;"></i></span>
        <p>{{countTotalPrice(productPurchaseSpecificationMsg).num}}</p>
        <p>{{countTotalPrice(productPurchaseSpecificationMsg).sum}}</p>
      </div>
      <p></p>
      <input type="number" v-model="productPurchaseFreight">
      <p></p>
      <input type="text" v-model="productPurchaseShortage" placeholder="productPurchaseShortage">
      <p style="color: red;"></p>
      <input type="text" style="color: red;" v-model="productPurchaseSpecificAttention">
      <p></p>
      <input type="text" v-model="productPurchaseExplain">
      <div>
        <button @click="submitUpdateData"></button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'add-new-purchase-msg',
  data () {
    return {
      msg: '',
      // --------------------------------------------------------------------------------------------  productPurchaseData
      productPurchaseDate: new Date(), // 
      productPurchaseSeller: '', // 
      productPurchaseName: '', // 
      productPurchaseSpecificationMsg: [ // id +  +  +  + 
        {productPurchaseId: '', productPurchaseNum: 0, productPurchaseCost: 0, priceDiffer: 0}
      ],
      productPurchaseFreight: 0, // 
      productPurchaseShortage: false, // 
      productPurchaseSpecificAttention: false, // 
      productPurchaseExplain: '', // 
      // --------------------------------------------------------------------------------------------
      msgHintListData: '', //  
      nameMsgHintList: { // 
        show: false, // 
        data: '', // 
        top: 0, // 
        left: 0, // 
        width: 0, // 
        forecastOption: false, // 
        alreadyOption: '' // 
      },
      mustFilled: false, // 
      goodsListDB: [], // []
      originalPurchaseData: { // id
        productPurchaseSeller: '', // 
        productPurchaseName: '', // 
        productPurchaseSpecificationMsg: [ // id +  +  +  + 
          {productPurchaseId: '', productPurchaseNum: 0, productPurchaseCost: 0, priceDiffer: 0}
        ],
        productPurchaseFreight: 0, // 
        productPurchaseShortage: false, // 
        productPurchaseSpecificAttention: false, // 
        productPurchaseExplain: '' // 
      },
      searchData: {
        _id: ''
      },
      purchase_id: 'add', // _id
      updateData: { // 
        productPurchaseSeller: '', // 
        productPurchaseName: '', // 
        productPurchaseSpecificationMsg: [ // id +  +  +  + 
          {productPurchaseId: '', productPurchaseNum: 0, productPurchaseCost: 0, priceDiffer: 0}
        ],
        productPurchaseFreight: 0, // 
        productPurchaseShortage: false, // 
        productPurchaseSpecificAttention: false, // 
        productPurchaseExplain: '' // 
      }
    }
  },
  created () {
    var cvm = this
    this.productPurchaseDate = new Date().getFullYear() + '' + new Date().getMonth() + 1 + '' + new Date().getDate() + '' + new Date().getHours() + ':' + new Date().getMinutes()
    cvm.purchase_id = cvm.$route.query._id
    cvm.searchData._id = cvm.$route.query._id
    cvm.getPurchaseMsgByPurchaseId() // Id
  },
  watch: {
    '$route' (to, from) { // 
      // console.log(to, from)
      var wvm = this
      wvm.purchase_id = wvm.$route.query._id // _id
    },
    'productPurchaseName' (newData, oldData) {
      console.log('')
      this.acquireProductId()
    }
  },
  methods: {
    /* ------------------------------------------------------------------ */
    // 

    // //
    getProductNameByInput (inputValue, itemKey) {
      console.log('')
      this.$sendRequest.RTSPost('/rm_goods/find_searchItem', {userId: 'id', searchData: {productName: {$regex: inputValue}}, searchItem: itemKey}).then(res => { // searchData
        if (res.data.code === 0) {
          this.msgHintListData = res.data.data
          console.log(this.msgHintListData)
        } else if (res.data.code === 1) {
          console.log('')
          this.$windowFn.allWindow('AddNewPurchaseMsg', 'remind', '' + res.data.msg + '', {agree: '', cancel: ''}) // 
        }
      })
    },

    // Id
    getProductIdByProductName () {
      console.log('')
      this.$sendRequest.RTSPost('/rm_goods/goods_by_searchData', {userId: 'id', searchData: {productName: this.productPurchaseName}}).then(res => { // searchData
        if (res.data.code === 0) {
          this.goodsListDB = res.data.data
          console.log(this.goodsListDB)
        } else if (res.data.code === 1) {
          console.log('')
          this.$windowFn.allWindow('AddNewPurchaseMsg', 'remind', '' + res.data.msg + '', {agree: '', cancel: ''}) // 
        }
      })
    },

    // _id
    getPurchaseMsgByPurchaseId () {
      if (this.purchase_id !== 'add') {
        this.$sendRequest.RTSPost('/rm_purchases/purchases_by_searchData', {userId: 'Id', searchData: this.searchData}).then(res => {
          if (res.data.code === 0) {
            this.originalPurchaseData = res.data.data
            console.log(this.originalPurchaseData)
          }
        })
      } else {
        console.log('')
      }
    },

    // 

    /* --------------------------------------------------------------------------------- */
    // 
    goAnchor (selector) {
      this.$el.querySelector(selector).scrollIntoView() // querySelectorcssscrollIntoViewtruefalse
    },

    // 
    countTotalPrice (arrayValue) {
      console.log('')
      var sum = 0
      var num = 0
      arrayValue.forEach(e => {
        sum += e.productPurchaseNum * e.productPurchaseCost
        num += Number(e.productPurchaseNum)
      })
      return {num, sum}
    },

    // 
    productPurchaseMsgBox () {
      console.log('productPurchaseData')
      return {
        productPurchaseSeller: this.productPurchaseSeller, // 
        productPurchaseName: this.productPurchaseName, // 
        productPurchaseSpecificationMsg: this.productPurchaseSpecificationMsg, // id +  +  +  + 
        productPurchaseFreight: this.productPurchaseFreight, // 
        productPurchaseShortage: this.productPurchaseShortage, // 
        productPurchaseSpecificAttention: this.productPurchaseSpecificAttention, // 
        productPurchaseExplain: this.productPurchaseExplain// 
      }
    },

    // 
    inputFunction (value) {
      console.log('', value)
      if (value === 'purchaseSeller') { //  // 

      } else if (value === 'purchaseName') { //  // id
        console.log('')
        this.getProductNameByInput(this.productPurchaseName, 'productName') // 
      }
    },

    // 
    showProductNameList () {
      console.log('')
      this.nameMsgHintList.show = !this.nameMsgHintList.show
      this.getProductNameByInput(this.productPurchaseName, 'productName')
    },

    // id
    acquireProductId () {
      if (this.productPurchaseName === '') {
        console.log('')
        this.mustFilled = 'name' // 
        this.goAnchor('#productName') // 
      } else {
        console.log('')
        this.getProductIdByProductName(this.productPurchaseName) // 
      }
    },

    // ({Id:'', :'', :''})
    addSpecifications () {
      // console.log('')
      this.productPurchaseSpecificationMsg.push({productPurchaseId: '', productPurchaseNum: 0, productPurchaseCost: 0})
    },

    // 
    deleteSpecifications (ruleIndex) {
      // console.log('' + ruleIndex + '')
      this.productPurchaseSpecificationMsg.splice(ruleIndex, 1)
    },

    // 
    submitPurchaseData () {
      console.log('')
      if (this.productPurchaseSpecificationMsg.length === 0 || this.productPurchaseSpecificationMsg[0].productPurchaseId === '') {
        console.log('')
        this.mustFilled = 'productPurchaseSpecificationMsg'
        this.goAnchor('#productPurchaseSpecificationMsg')
      } else {
        var productPurchaseData = this.productPurchaseMsgBox()
        this.$sendRequest.RTSPost('/rm_purchases/add_purchases', productPurchaseData).then(res => {
          if (res.data.code === 0) {
            // console.log('');
            this.$windowFn.allWindow( // 
              'AddNewPurchaseMsg',
              'congratulations',
              {
                text: '' + this.productPurchaseName + ''
              },
              {agree: '', cancel: ''},
              true
            ).then(e => {
              if (e.code === 0) {
                console.log('')
                this.$router.push({name: 'PurchasesList'}) // GoodsList.vue
              } else if (e.code === 1) {
                console.log('')
                // 
                this.productPurchaseDate = new Date() // 
                this.productPurchaseSeller = '' // 
                this.productPurchaseName = '' // 
                this.productPurchaseSpecificationMsg = [] // id +  +  +  + 
                this.productPurchaseFreight = 0 // 
                this.productPurchaseShortage = false // 
                this.productPurchaseSpecificAttention = false // 
                this.productPurchaseExplain = '' // 
              }
            })
          } else {
            console.log('')
          }
        })
      }
    },

    // idproductPurchaseCost
    boundChargePrice (inputIndex, productId) { // inputIndexproductIdid
      console.log('', inputIndex, productId)
      this.goodsListDB.filter(e => {
        if (e.productId === productId) {
          this.productPurchaseSpecificationMsg[inputIndex].productPurchaseCost = e.productOriginalPrice
        }
      })
    },

    // id
    productChargePriceByProductId (productId) {
      console.log('id', 'productId: ', productId, this.goodsListDB)
      if (this.goodsListDB.length !== 0 && productId !== '') {
        var productMsg = this.goodsListDB.filter(e => {
          return e.productId === productId
        })
        return productMsg[0].productOriginalPrice
      }
    },

    // 
    contrastChargePrice (inputIndex, productId, nowCost) {
      console.log('')
      this.productPurchaseSpecificationMsg[inputIndex].priceDiffer = nowCost - this.productChargePriceByProductId(productId)
    },

    // 
    submitUpdateData () {
      console.log('')
      if (this.productPurchaseSpecificationMsg.length === 0 || this.productPurchaseSpecificationMsg[0].productPurchaseId === '') {
        console.log('')
        this.mustFilled = 'updateProductPurchaseSpecificationMsg'
        this.goAnchor('#updateProductPurchaseSpecificationMsg')
      } else {
        this.updateData = this.productPurchaseMsgBox()
        console.log(this.updateData)
        this.$sendRequest.RTSPost('/rm_purchases/update_purchases', {updateData: this.updateData, _id: this.searchData._id, userId: this.userId}).then(res => {
          if (res.data.code === 0) {
            // console.log('');
            this.$windowFn.allWindow( // 
              'AddNewPurchaseMsg',
              'congratulations',
              {
                text: '' + this.productPurchaseName + ''
              },
              {agree: '', cancel: ''},
              true
            ).then(e => {
              if (e.code === 0) {
                console.log('')
                this.$router.push({name: 'PurchasesList'}) // GoodsList.vue
              } else if (e.code === 1) {
                console.log('')
                // 
                this.productPurchaseDate = new Date() // 
                this.productPurchaseSeller = '' // 
                this.productPurchaseName = '' // 
                this.productPurchaseSpecificationMsg = [] // id +  +  +  + 
                this.productPurchaseFreight = 0 // 
                this.productPurchaseShortage = false // 
                this.productPurchaseSpecificAttention = false // 
                this.productPurchaseExplain = '' // 
              }
            })
          } else {
            console.log('')
          }
        })
      }
    }
    /*
    * 
    * */
    // this.msgHintList.show = true // 
    // this.msgHintList.top = event.target.offsetTop + event.target.offsetHeight //  + 
    // this.msgHintList.left = event.target.offsetLeft // 
    // this.msgHintList.width = event.target.offsetWidth // 
    // test () {
    //   console.log('ABC-test')
    // }
  }
}
</script>

<style scoped lang="less">
  .optionListActive{ /*  */
    background: #0086b3;
    color: #e4af91;
  }
</style>



