// ...
let textOmit = (value, num) => {
  if (!value) return
  if (value.length > num) {
    return value.substring(0, num) + '...'
  }
  return value
}

// ()
let textCutOut = (value, num) => {
  if (!value) return
  if (value.length > num) {
    return value.substring(0, num)
  }
  return value
}

// 
let EnglishTranslate = (value) => {
  if (!value) return ''
  switch (value) {
    case 'onlineBuyer':
      return ''
    case 'boss':
      return ''
    case 'assistant':
      return ''
    case 'identity':
      return ''
    case 'userName':
      return ''
    case 'admin':
      return ''
    case 'name':
      return ''
    case 'id':
      return ''
  }
}

export {textOmit, textCutOut, EnglishTranslate}



