<template>
    <div id="ShowBigImage" v-if="show">
      <p class="AppAllExplain">{{msg}}</p>
      <div class="AllBaseBackgroundMasking" v-on:click="close"></div><!--base.less-->
      <div class="image">
        <div class="imageBox">
          <img :src="image" alt="">
        </div>
        <div class="closeShow">
          <span class="text" v-on:click="close"></span>
        </div>
      </div>
    </div>
</template>

<script>
export default {
  name: 'show-big-image',
  data () {
    return {
      msg: '',
      show: false, // 
      image: '' // 
    }
  },
  mounted: function () {

  },
  methods: {
    // 
    close () {
      console.log('')
      this.show = false
    }
  }
}
</script>

<style scoped lang="less">
@import '../../../assets/less/main'; /* main.less */

#ShowBigImage{
  .image{
    z-index: 11; /*Masking10*/
    position: fixed;
    left: 0;
    right: 0;
    top: 50%;
    margin: auto;
    .transform(translate(0, -50%));
    width: 1000px;
    .border_radius(8px);
    overflow: hidden;
    text-align: center;
    .imageBox{
      padding: 10px;
      background: @color-secondary; /*  */
      >img{
        width: 100%;
      }
    }
    .closeShow{
      .text{
        cursor: pointer;
        font-size: 60px;
        color: #fff;
      }
      .text:active{ /*  */
        position: relative;
        top: 5px; /*  */
        color: black;
      }
    }
  }
}
</style>



