import Vue from 'vue'
import commonIndex from '../../components/common/commonIndex'

// loading
// var NormalLoading = commonIndex.normalLoading
// window
var WarningRemindWindow = commonIndex.warningRemindWindow
var LoginWindowCom = commonIndex.loginWindow
// 
var ShowBigImage = commonIndex.showBigImage

var bus = new Vue()

// 
const allWindow = function ( // id{agree: '', cancel: ''}true/false
  fatherLabel,
  category,
  content,
  operate,
  callBack
) {
  var label = fatherLabel || 'body' // 
  callBack = callBack || false // 
  if (label !== 'body') { // body #
    // console.log(' body')
    label = '#' + label
  }
  // 
  var option =
    {
      show: true,
      windowGenre: category, // 
      titleColor: '', // 
      titleText: '', // 
      contentText: content, // 
      operateText: operate, // 
      callBackFn: callBack // 
    }
  // /
  var callBackFn
  if (callBack) {
    // console.log('')
    callBackFn = new Promise(resolve => {
      bus.$on('windows-confirmAgree', (e) => { // windows-confirmAgree
        // console.log('', e)
        resolve(e)
      })
    })
  } else {
    // console.log('')
  }
  /* --------------------------------------------------------------- */
  switch (category) { // 
    case 'warning': // ['./src/components/Home/AddNewGoods.vue', './src/components/ListShow/GoodsList.vue']
      // console.log('windowFn.js')
      option.titleColor = 'red' // 
      option.titleText = '' // 
      var warningWindowComponent = new WarningRemindWindow({
        data: option
      }).$mount()
      document.querySelector(label).appendChild(warningWindowComponent.$el)
      // Promiseresolve
      /*
      * new Promise(resolve => {
        bus.$on('windows-confirmAgree', (e) => { // windows-confirmAgree
          resolve(e)
        })
      })
      */
      return callBackFn // 
    case 'remind': // ['./src/components/Home/AddNewGoods.vue', './src/components/Home/GoodsList.vue']
      // console.log('windowFn.js')
      option.titleColor = 'orange' // 
      option.titleText = '' // 
      var remindWindowComponent = new WarningRemindWindow({
        data: option
      }).$mount()
      document.querySelector(label).appendChild(remindWindowComponent.$el)
      return callBackFn // 
    case 'congratulations': //   ['./src/components/Home/AddNewGoods.vue']
      console.log('windowFn.js')
      option.titleColor = 'green' // 
      option.titleText = '' // 
      var congratulateWindowComponent = new WarningRemindWindow({
        data: option
      }).$mount()
      document.querySelector(label).appendChild(congratulateWindowComponent.$el)
      return callBackFn // 
  }
}

// 
const showImage = function (fatherLabel, data) {
  console.log('')
  var label = fatherLabel || 'body' // 
  if (label !== 'body') { // body #
    // console.log(' body')
    label = '#' + label
  }
  data.show = true // 
  var ShowBigImageComponent = new ShowBigImage({
    data: data
  }).$mount()
  document.querySelector(label).appendChild(ShowBigImageComponent.$el)
}

// 
const openLoginWindow = function (fatherLabel, data, afterCloseFn, loginSuccessNextFn) { // 
  console.log('')
  var label = fatherLabel || 'body' // 
  if (label !== 'body') { // body #
    // console.log(' body')
    label = '#' + label
  }
  data.show = true // 
  data.afterCloseFn = afterCloseFn || function () {} // 
  data.loginSuccessNextFn = loginSuccessNextFn || function () {} // 
  var loginWindowComponent = new LoginWindowCom({
    data: data
  }).$mount()
  document.querySelector(label).appendChild(loginWindowComponent.$el)
}

export default {bus, allWindow, showImage, openLoginWindow}

