<template>
    <div id="WarningRemindWindow" v-if="show">
      <p class="AppAllExplain">{{msg}}</p>
      <div class="AllBaseBackgroundMasking" v-on:click="close"></div><!--base.less-->
      <div class="window">
        <p class="AppAllExplain">{{msg}}</p>
        <div class="head">
          <span class="close" v-on:click="close"></span>
        </div>
        <div class="body">
          <div class="title">
            <span v-bind:style="{color: titleColor}" v-if="windowGenre === 'warning' || windowGenre === 'remind'"><i class="iconfont icon-jinggao"></i></span>
            <span v-bind:style="{color: titleColor}" v-if="windowGenre === 'congratulations'"><i class="iconfont icon-chenggongicon"></i></span>
            <span class="titleText">{{titleText}}</span>
          </div>
          <div class="contentTextBox" v-if="typeof contentText === 'string'">
            <p>{{contentText}}</p>
          </div>
          <div class="contentTextBox" v-if="typeof contentText === 'object'">
            <p>{{contentText.text}}</p>
            <p class="imageListBox"><span class="imageEachBox" v-for="(item, index) in contentText.image" :key="index + 'window'"><img :src="item" alt=""></span></p>
          </div>
        </div>
        <div class="footer">
          <!--  -->
          <div class="warning" v-if="callBackFn">
            <span class="confirm" v-on:click="operateBtn(true)">{{operateText.agree}}</span>
            <span class="cancel" v-on:click="operateBtn(false)">{{operateText.cancel === ''? '' : operateText.cancel}}</span>
          </div>
          <div class="remind" v-if="!callBackFn">
            <span class="confirm" v-on:click="close">{{operateText.agree}}</span>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
export default {
  name: 'warning-remind-window',
  data () {
    return {
      msg: 'oror',
      show: false, // 
      windowGenre: '', // 
      titleColor: '', // 
      titleText: '', // 
      contentText: '', // 
      operateText: { // 
        agree: '', // true
        cancel: '' // false
      },
      callBackFn: false // 
    }
  },
  mounted: function () {

  },
  methods: {
    // 
    close () {
      console.log('')
      this.show = false
    },
    // or
    operateBtn (option) {
      console.log('')
      var windowData = { // 
        code: 2,
        msg: 'W-RWindow'
      }
      if (option) {
        console.log('')
        windowData = { // 
          code: 0,
          msg: 'W-RWindow'
        }
      } else {
        console.log('')
        windowData = { // 
          code: 1,
          msg: 'W-RWindow'
        }
      }
      this.$windowFn.bus.$emit('windows-confirmAgree', windowData) // bus
      this.close() // 
    }
  }
}
</script>

<style scoped lang="less">
  @import '../../../assets/less/main';

  #WarningRemindWindow{
  .window{
    z-index: 11; /*Masking10*/
    position: fixed;
    left: 0;
    right: 0;
    top: 50%;
    margin: auto;
    .transform(translate(0, -50%));
    width: 500px;
    background: #fff;
    .border_radius(8px);
    overflow: hidden;
    text-align: center;
    .head{
      height: 44px;
      background: @color-secondary;
      .close{
        float: right;
        margin-right: 10px;
        font-size: 30px;
        cursor: pointer;
        color: #fff;
      }
      .close:hover{

      }
    }
    .body{
      border-top: 1px solid #666;
      padding: 10px 20px;
      text-align: center;
      .title{
        font-size: 20px;
        font-weight: 700;
        .titleText{
          margin-left: 4px;
        }
      }
      .contentTextBox{
        padding: 20px 10px;
        .imageListBox{
          margin-top: 10px;
          .imageEachBox{
            display: inline-block;
            width: 50px;
            height: 50px;
            margin: 0 5px;
            >img{
              width: 100%;
              height: 100%;
            }
          }
        }
      }
    }
    .footer{
      padding-bottom: 20px;
      .warning,.remind{
        >span{
          border: 1px solid @color-secondary; // 
          padding: 4px 10px;
          .border_radius(4px);
          cursor: pointer;
          &.confirm{
            background: @color-secondary-tint; // 
            color: #fff;
            margin-right: 4px;
          }
          &.cancel{
            color: @color-secondary; // 
            margin-left: 4px;
          }
        }
      }
    }
  }
}
</style>



