<template>
    <div id="UserPersonalCenter">
      <p class="AppAllExplain">{{msg}}</p>
        <h3>{{$store.state.user.userName}}</h3>
        <h4>{{$store.state.user.identity}}</h4>
        <button @click="loginOut"></button>
    </div>
</template>

<script>
export default {
  name: 'user-personal-center',
  data () {
    return {
      msg: '',
      operateUser: { // vuex
        userId: '',
        userName: '',
        identity: ''
      }
    }
  },
  created () {
    this.operateUser = this.$store.state.user
    if (this.operateUser.userId && this.operateUser.userName && this.operateUser.identity) {
      console.log('')
    } else {
      console.log('')
      this.$router.push('/Home') // 
    }
  },
  methods: {
    /*
    * 
    * */
    /*
    * 
    * */
    // 
    loginOut () {
      var vm = this
      console.log('')
      this.$sendRequest.RTSGet('/rm_users/user_logout', this.$store.state.user).then(res => {
        console.log(res)
        var status // 
        if (res.data.code === 0) { // 
          status = 'success'
          vm.$router.push('/Home') // 
        } else {
          status = 'warning'
          //   cookiessession 
          // Cookiesvuexstoreuser
        }
        this.$store.state.user = {userId: '', userName: '', identity: ''} // vuexstoreuser
        this.$message({
          message: res.data.msg,
          type: status
        })
      })
    }
  }
}
</script>

<style scoped>

</style>



