<template>
  <div id="LoginWindow" v-if="show">
    <p class="AppAllExplain">{{msg}}</p>
    <div class="AllBaseBackgroundMasking" v-on:click="close"></div><!--base.less-->
    <div class="window">
      <p class="AppAllExplain">{{msg}}</p>
      <div class="head">
        <span class="close" v-on:click="close"></span>
      </div>
      <div class="body">
        <div class="inputBox" title="">
          <span>&nbsp;&nbsp;</span>
          <input type="text" v-model="user.userName" placeholder="/">
        </div>
        <div class="inputBox">
          <span>&nbsp;&nbsp;</span>
          <input type="password" v-model="user.userPassword" @keyup.enter="operateBtn(true)">
        </div>
        <div class="optionBox">
          <p class="optionText">
            <el-checkbox v-model="rememberPassword" :disabled="user.userName === '' || user.userPassword === ''" @change="changePassword(rememberPassword, user.userName, user.userPassword)"></el-checkbox>
          </p>
          <p class="errorMsgText" v-if="errorMsg">{{errorMsg}}</p>
        </div>
      </div>
      <div class="footer">
        <span class="confirm" v-on:click="operateBtn(true)"></span>
        <span class="cancel" v-on:click="operateBtn(false)"></span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'login-window',
  data () {
    return {
      msg: '',
      show: false, // 
      afterCloseFn: '', // 
      loginSuccessNextFn: '', // 
      user: {
        userName: '',
        userPassword: ''
      },
      errorMsg: false, // 
      rememberPassword: false, // 
      loginSuccess: false // 
    }
  },
  created () {
    this.user.userName = localStorage.getItem('userName') === null ? '' : localStorage.getItem('userName') // localStorage
    this.user.userPassword = localStorage.getItem('userPassword') === null ? '' : localStorage.getItem('userPassword') // localStorage
    if (this.user.userPassword !== '') { // ''nullDecryptEncrypt(null)Encrypt('')
      this.user.userPassword = this.$jsfn.Decrypt(this.user.userPassword)
    }
    if (this.user.userName !== '' && this.user.userPassword !== '') { // 
      this.rememberPassword = true
    }
  },
  methods: {
    // 
    close () {
      console.log('')
      this.show = false
      if (!this.loginSuccess) {
        this.afterCloseFn()
      } else {
        console.log('')
      }
    },
    // 
    emptyShow (value) {
      console.log('')
      if (this.user.userName === '') {
        this.errorMsg = (value === 'login' ? '' : '') + ''
      } else if (this.user.userPassword === '') {
        this.errorMsg = (value === 'login' ? '' : '') + ''
      } else {
        this.errorMsg = true
      }
    },

    // 
    changePassword (event, userName, userPassword) {
      if (event) { // 
        console.log('')
        // Session Storage
        localStorage.setItem('userName', userName)
        localStorage.setItem('userPassword', this.$jsfn.Encrypt(userPassword)) // crypto-js
      } else {
        console.log('')
        localStorage.removeItem('userName')
        localStorage.removeItem('userPassword')
      }
    },

    // or
    operateBtn (option) {
      var vm = this
      console.log('')
      if (option) {
        this.emptyShow('login') // 
        console.log('', this.errorMsg)
        if (this.errorMsg === true) {
          this.$sendRequest.RTSPost('/rm_users/user_login', {user: this.user}).then(res => {
            console.log(res)
            this.errorMsg = res.data.msg // 
            var status // 
            if (res.data.code === 0) { // 
              status = 'success'
              console.log(res.data.data)
              this.changePassword(vm.rememberPassword, vm.user.userName, vm.user.userPassword) // 
              this.$store.commit('updateUserInfo', res.data.data)
              this.loginSuccess = true
              this.close() // 
              this.loginSuccessNextFn()// 
            } else {
              status = 'warning'
            }
            this.$message({
              message: res.data.msg,
              type: status
            })
          })
        }
      } else {
        console.log('')
        this.close() // 
      }
    }
  }
}
</script>

<style scoped lang="less">
  @import '../../../assets/less/main';

  #LoginWindow{
    .window{
      z-index: 11; /*Masking10*/
      position: fixed;
      left: 0;
      right: 0;
      top: 50%;
      margin: auto;
      .transform(translate(0, -50%));
      width: 500px;
      background: #fff;
      .border_radius(8px);
      overflow: hidden;
      .head{
        height: 44px;
        background: @color-secondary;
        .close{
          float: right;
          margin-right: 10px;
          font-size: 30px;
          cursor: pointer;
          color: #fff;
        }
        .close:hover{

        }
      }
      .body{
        border-top: 1px solid #666;
        padding: 10px 20px;
        .inputBox{
          padding: 22px 16px;
          color: @color-primary;
          font-family: 'STHupo';
          font-size: 22px;
          >input{
            font-size: 16px;
            width: 320px;
            padding: 4px;
            .border_radius(6px);
            border: 1px solid @color-secondary;
            .box_shadow(1px 1px 4px @color-secondary-deep);
          }
        }
        .optionBox{
          text-align: right;
          padding: 0 22px 0 16px;
          >p{
            display: inline-block;
            &.errorMsgText{
              float: left;
              color: red;
            }
          }

        }
      }
      .footer{
        padding-bottom: 20px;
        text-align: center;
        font-weight: 900;
        span{
            border: 1px solid @color-secondary; // 
            padding: 4px 10px;
            .border_radius(4px);
            cursor: pointer;
            &.confirm{
              background: @color-secondary-tint; // 
              color: #fff;
              margin-right: 4px;
            }
            &.cancel{
              color: @color-secondary; // 
              margin-left: 4px;
            }
          }
      }
    }
  }
</style>



