// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue' // vue
import App from './App'
import router from './router'

/*  */
import axios from 'axios' // axios
import './assets/plugin/iconfont/iconfont.css' // 
import VueLazyLoad from 'vue-lazyload' // 
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import md5 from 'js-md5' // md5
import store from './assets/Vuex/store' // Vuex

/*  */
import jsfn from './assets/js/jsfn' // 
import sendRequest from './assets/js/sendRequest' // 
import windowFn from './assets/js/windowFn' // 
import textShowFn from './assets/js/textShowFn' // 

/*  */
import * as countFilter from './assets/js/filters/countFilter' // 
import * as textFilter from './assets/js/filters/textFilter' // 

Vue.config.productionTip = false // vue

axios.defaults.withCredentials = true // nodecookieApplicationcookies
Vue.prototype.$http = axios // axios
Vue.prototype.$jsfn = jsfn // jsfn
Vue.prototype.$sendRequest = sendRequest // sendRequest
Vue.prototype.$windowFn = windowFn // windowsFn
Vue.prototype.$textShowFn = textShowFn // fullDisplayText
Vue.prototype.$md5 = md5 // md5
Vue.prototype.$store = store // Vuexstore

for (var keyC in countFilter) { // 
  Vue.filter(keyC, countFilter[keyC])
}
for (var keyT in textFilter) { // 
  Vue.filter(keyT, textFilter[keyT])
}

// 
Vue.use(VueLazyLoad, {
  preload: 1.3, // 
  loading: require('./assets/image/YMTloading.jpg'), // loading
  error: require('./assets/image/shark.jpg'), // 
  attempt: 3 // 
  // listenEvents:['scroll','wheel','mousewhell','resize','animationend','transitionend','touchhmove'] // vue
})

Vue.use(ElementUI)

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router, // 
  components: { App },
  template: '<App/>'
})

// console.log = function () {} // console.log



