<template>
    <div id="ChooseMyBoss">
      <p class="AppAllExplain">{{msg}}</p>
      <!--  -->
      <div>
        <!---->
        <div style="text-align: center; padding: 30px;">
          <input style="padding: 5px; font-size: 18px; width: 500px;" type="text" placeholder="......" v-model="searchData.userName" @keyup.enter="getUsersListBySearch">
          <button @click="getUsersListBySearch"></button>
        </div>
        <table>
          <thead>
            <tr>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(user, index) in usersList" :key="index">
              <td style="text-decoration: underline; color: deepskyblue; cursor: pointer;" @click="chooseBoss(user._id, user.userName)">{{user.userName}}</td>
              <td>{{user.telephone}}</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
</template>

<script>
export default {
  name: 'choose-my-boss',
  data () {
    return {
      msg: '',
      usersList: [], // 
      operateUser: {}, // 
      searchData: {
        identity: 'boss', // 
        userName: '' // 
      }
    }
  },
  created () {
    var vm = this
    this.operateUser = this.$jsfn.determineUserLoginSuccess() // 'src/assets/js/jsfn.js'
    if (this.operateUser) {
      console.log('')
      vm.getUsersListBySearch()
    } else {
      console.log('', vm.operateUser)
      vm.$message({
        type: 'warning',
        message: ''
      })
    }
    // User.vue
  },
  methods: {
    /*
    * 
    * */
    // 
    getUsersListBySearch () {
      console.log('', this.searchData)
      this.$sendRequest.RTSPost('/rm_users/users_by_searchData', {operateUser: 'id', searchData: this.searchData}).then(res => {
        // console.log('')
        if (res.data.code === 0) {
          // console.log('')
          console.log(res)
          this.usersList = res.data.data
        } else if (res.data.code === 1) {
          // console.log('')
          this.$windowFn.allWindow('GoodsList', 'remind', res.data.msg + '', {agree: '', cancel: ''}) // 
        }
      })
    },
    /*
    * 
    * */
    /* --------------------------- */

    /* ------------------------------- */
    //  ----------------------------------------------------------------to-do
    chooseBoss (userId, userName) {
      console.log('', userId, userName)
      this.$sendRequest.RTSPost('/rm_users/assistant_choose_myBoss', {operateUser: this.operateUser, updateData: {myBoss: userName}, bossId: userId}).then(res => {
        console.log(res)
        var status
        if (res.data.code === 0) {
          status = 'success'
          this.$router.push({name: 'UserMsg'}) // 
        } else {
          status = 'warning'
        }
        this.$message({
          message: res.data.msg,
          type: status
        })
      })
    }
  }
}
</script>

<style scoped>

</style>



