<!--  2019/01/08 -->
<template>
  <div id="PurchasesList">
    <p class="AppAllExplain">{{msg}}</p>
    <!---->
    <div>
      <!---->
      <div style="text-align: center; padding: 30px;">
        <input style="padding: 5px; font-size: 18px; width: 500px;" type="text" placeholder="......" v-model="searchData.inputCondition" @keyup.enter="getPurchasesBySearchData"
        ><button @click="getPurchasesBySearchData"></button>
      </div>
      <div style="float: right; margin-right: 20px;">
        <button @click="switchoverProductList(false)" v-bind="{disabled: !searchData.exist}"></button>
      </div>
      <div style="float: right; margin-right: 20px;">
        <button @click="switchoverProductList(true)" v-bind="{disabled: searchData.exist}"></button>
      </div>
      <div>
        <span></span>
        <select v-model="searchData.productPurchaseDate" v-on:change="getPurchasesDataBySearchData">
          <option value=""></option>
          <option :value="time._id" v-for="(time, index) in searchOneItemData" :key="index" @click="getPurchasesDataBySearchData">{{time._id | textCutOut(10)}}</option>
        </select>
      </div>
      <!---->
      <table>
        <thead>
          <tr style="border: 1px solid steelblue;">
            <th><input type="checkbox" v-on:change="checkAll($event)" :checked="$jsfn.contrastArray(DBIdArray, allIdArray)"></th>
            <th></th>
            <th style="border-left: 1px solid steelblue;"></th>
            <th style="border-left: 1px solid steelblue;"></th>
            <th style="border-left: 1px solid steelblue;"></th>
            <th style="border-left: 1px solid steelblue;"></th>
            <th style="border-left: 1px solid steelblue;"></th>
            <th style="border-left: 1px solid steelblue;"></th>
            <th style="border-left: 1px solid steelblue;"></th>
            <th style="border-left: 1px solid steelblue; color: red;"></th>
            <th style="border-left: 1px solid steelblue;"></th>
            <th style="border-left: 1px solid steelblue;"></th>
            <th style="border-left: 1px solid steelblue;"></th>
            <th style="border-left: 1px solid steelblue;"></th>
          </tr>
        </thead>
        <tbody>
          <tr style="border: 1px solid steelblue;" v-for="(purchaseMsg, index) in goodsPurchasesList" :key="index">
            <td><input type="checkbox" v-on:change="arrangeDBIdArray($event, purchaseMsg._id)" :checked="DBIdArray.indexOf(purchaseMsg._id) === -1? false : true"></td>
            <td>{{index + 1}}</td>
            <td>{{purchaseMsg.productPurchaseSeller}}</td>
            <td>{{purchaseMsg.productPurchaseName}}</td>
            <td>
              <table>
                <thead>
                  <tr>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(specificMsg, msgIndex) in purchaseMsg.productPurchaseSpecificationMsg" :key="msgIndex">
                    <td>{{specificMsg.productPurchaseId}}</td>
                    <td>{{specificMsg.productPurchaseCost}}</td>
                    <td>{{specificMsg.productPurchaseNum}}</td>
                    <td>{{specificMsg.productPurchaseCost * specificMsg.productPurchaseNum}}</td>
                  </tr>
                </tbody>
              </table>
            </td>
            <td>{{countTotalPriceNum(purchaseMsg.productPurchaseSpecificationMsg).num}}</td>
            <td>{{countTotalPriceNum(purchaseMsg.productPurchaseSpecificationMsg).sum}}</td>
            <td>{{purchaseMsg.productPurchaseFreight}}</td>
            <td>{{purchaseMsg.productPurchaseShortage === 'false'? '' : purchaseMsg.productPurchaseShortage}}</td>
            <td style="color: red;">{{purchaseMsg.productPurchaseSpecificAttention === 'false'? '' : purchaseMsg.productPurchaseSpecificAttention}}</td>
            <td>{{purchaseMsg.productPurchaseExplain}}</td>
            <td>{{purchaseMsg.meta.createAt}}</td>
            <td>{{purchaseMsg.meta.updateAt | countDateChinese}}</td>
            <td>
              <button @click="operatePurchaseMsg('update', purchaseMsg)"></button>
              <button @click="operatePurchaseMsg('delete', purchaseMsg)"></button>
              <button @click="operatePurchaseMsg('admin_delete', purchaseMsg)"></button>
            </td>
          </tr>
        </tbody>
      </table>
      <div>
        <button @click="batchDelete"></button>
        <button @click="invertSelection"></button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'purchases-list',
  data () {
    return {
      msg: '',
      goodsPurchasesList: [], // 
      searchData: {
        inputCondition: '', // 
        productPurchaseDate: '', // 
        exist: true // 
      },
      userId: '', // Id
      searchOneItemData: [], // 
      DBIdArray: [], // _id()
      allIdArray: [], // _id
      allItem: false // 
    }
  },
  created () {
    var vm = this
    vm.getDataList()
    vm.getPurchaseItemBySearch('productPurchaseDate')
  },
  methods: {
    /*
    * 
    * */
    // 
    getDataList () {
      // exist=trueboss
      this.$sendRequest.RTSGet('/rm_purchases/complete_purchases', {userId: 'id'}).then(res => {
        // console.log('')
        if (res.data.code === 0) {
          // console.log('')
          console.log(res)
          this.goodsPurchasesList = res.data.data
          this.goodsPurchasesList.forEach(e => { // _id
            this.allIdArray.push(e._id)
          })
        } else if (res.data.code === 1) {
          // console.log('')
          this.$windowFn.allWindow('PurchasesList', 'remind', res.data.msg + '', {agree: '', cancel: ''}) // 
        }
      })
    },

    // 
    getPurchaseItemBySearch (searchItem) {
      console.log('')
      this.$sendRequest.RTSPost('/rm_purchases/find_allTime', {userId: this.userId, searchData: this.searchData, searchItem: searchItem}).then(res => {
        // console.log('')
        if (res.data.code === 0) {
          // console.log('')
          this.searchOneItemData = res.data.data
          console.log(res)
        } else if (res.data.code === 1) {
          // console.log('')
          this.$windowFn.allWindow('PurchasesList', 'remind', res.data.msg + '', {agree: '', cancel: ''}) // 
        }
      })
    },

    // 
    getPurchasesDataBySearchData () {
      console.log('')
      this.$sendRequest.RTSPost('/rm_purchases/purchases_by_searchData', {userId: this.userId, searchData: this.searchData}).then(res => {
        // console.log('')
        if (res.data.code === 0) {
          console.log('')
          console.log(res)
          this.goodsPurchasesList = res.data.data
          this.goodsPurchasesList.forEach(e => { // _id
            this.allIdArray.push(e._id)
          })
        } else if (res.data.code === 1) {
          // console.log('')
          this.$windowFn.allWindow('PurchasesList', 'remind', res.data.msg + '', {agree: '', cancel: ''}) // 
        }
      })
    },
    /*
    * 
    * */
    /* --------------------------- */

    /* ------------------------------- */

    // 
    countTotalPriceNum (arrayValue) {
      // console.log('')
      var sum = 0
      var num = 0
      arrayValue.forEach(e => {
        sum += e.productPurchaseNum * e.productPurchaseCost
        num += Number(e.productPurchaseNum)
      })
      return {num, sum}
    },

    // 
    operatePurchaseMsg (operate, purchaseMsg) {
      var vm = this
      const markedWords = function (data) { // 
        if (data.data.code === 0) {
          // console.log('')
          vm.getDataList() // 
        } else if (data.data.code === 1) {
          // console.log('')
          this.$windowFn.allWindow('PurchasesList', 'remind', data.data.msg + '', {agree: '', cancel: ''}) // 
        }
      }
      if (operate === 'delete') {
        console.log('existfalse')
        this.$windowFn.allWindow(
          'PurchasesList',
          'warning',
          '' + purchaseMsg.productPurchaseName + '10',
          {agree: '', cancel: ''},
          true
        ).then(res => { // 
          if (res.code === 0) {
            this.$sendRequest.RTSPost('/rm_purchases/update_purchases_shamDelete',
              {
                userId: 'Id',
                _id: purchaseMsg._id
              }).then(markedWords)
          }
        })
      } else if (operate === 'update') {
        console.log('')
        this.$router.push({name: 'AddNewPurchaseMsg', query: {_id: purchaseMsg._id}, params: {userId: 'Id'}}) // 
      } else if (operate === 'admin_delete') {
        console.log('')
        this.$windowFn.allWindow(
          'PurchasesList',
          'warning',
          '' + purchaseMsg.productPurchaseName + '',
          {agree: '', cancel: ''},
          true
        ).then(res => { // 
          if (res.code === 0) {
            this.$sendRequest.RTSDelete('/rm_purchases/delete_purchases', {
              userId: 'Id',
              purchases_id: purchaseMsg._id
            }).then(markedWords)
          }
        })
      }
    },

    /*
    * 
    * */
    // searchData
    getPurchasesBySearchData () {
      var vm = this
      console.log(vm.searchData)
      this.$sendRequest.RTSPost('/rm_purchases/purchases_by_searchData', {userId: 'Id', searchData: this.searchData}).then(res => {
        if (res.data.code === 0) {
          vm.goodsPurchasesList = res.data.data
          console.log(this.goodsPurchasesList)
        }
      })
    },

    //   vs 
    switchoverProductList (value) {
      console.log('  vs ')
      this.searchData.exist = value // 
      this.getPurchasesBySearchData() // 
    },

    /*
    *
    * */
    // _id
    arrangeDBIdArray (event, DBId) {
      if (event.target.checked) { // 
        // console.log('')
        if (this.DBIdArray.indexOf(DBId) === -1) { // 
          this.DBIdArray.push(DBId) // 
        } else {
          console.log('_id')
        }
      } else {
        console.log('')
        while (this.DBIdArray.indexOf(DBId) !== -1) { // 
          console.log('while')
          this.DBIdArray.splice(this.DBIdArray.indexOf(DBId), 1) // 
        }
      }
    },

    // 
    checkAll (event) {
      // console.log('', event.target.checked)
      if (event.target.checked) { // 
        this.DBIdArray = this.allIdArray.concat() // _id
      } else {
        this.DBIdArray = [] // 
      }
    },

    // 
    batchDelete () {
      console.log('', this.DBIdArray)
      this.$sendRequest.RTSDelete('/rm_purchases/delete_purchases_batch', {userId: this.userId, idArray: this.DBIdArray}).then(res => {
        if (res.data.code === 0) {
          console.log('')
          console.log('' + res.data.data.n + '') // -------------------------------------
        } else if (res.data.code === 1) {
          console.log('')
          this.$windowFn.allWindow('PurchasesList', 'remind', res.data.msg + '', {agree: '', cancel: ''}) // 
        }
      })
    },

    // 
    invertSelection () {
      console.log('')
      var idArray = this.DBIdArray.concat()
      this.DBIdArray = [] // 
      this.goodsPurchasesList.forEach(e => { // _id push_id
        if (idArray.indexOf(e._id) === -1) {
          this.DBIdArray.push(e._id)
        }
      })
    },

    /*
    * 
    * */
    abcD (event) {
      console.log(event)
    }
  }
}
</script>

<style scoped>

</style>



