<template>
    <div id="User">
      <p class="AppAllExplain">{{msg}}</p>
      <!---->
      <!---->
      <div>
        <p>User</p>
        <!---->
        <router-link to="/UserRegister"></router-link>
        <router-link :to="{name: 'UserMsg'}">|</router-link>
        <router-link to="/Home">|</router-link>
        <!--  -->
        <div v-if="operateUser.identity === 'admin'">
          <p style="color: red;">admin</p>
          <router-link :to="{name: 'UsersList'}">|</router-link>
        </div>
        <!--  -->
        <div v-if="operateUser.identity === 'boss'">
          <p style="color: red;">boss</p>
          <router-link to="/Home">|</router-link>
          <router-link to="/Home">|</router-link>
          <router-link to="/Home">|</router-link>
          <router-link to="/Home">|</router-link>
        </div>
        <div v-if="operateUser.identity === 'assistant'">
          <p style="color: red;">assistant</p>
          <router-link to="/Home">|</router-link>
          <router-link to="/Home">|</router-link>
          <router-link to="/Home">|</router-link>
          <router-link :to="{name: 'ChooseMyBoss'}" v-if="showChooseMyBoss">|</router-link>
        </div>
        <router-link to="/Home">|</router-link>
      </div>
      <!---->
      <router-view></router-view>
      <!---->
      <div>
        <p>User</p>
      </div>
      <!---->
    </div>
</template>

<script>
export default {
  name: 'User',
  data () {
    return {
      msg: '',
      operateUser: { // vuexuserId, userName, identity, myBoss, chooseStoreOwner
        identity: ''
      },
      showChooseMyBoss: false // 
    }
  },
  created () {
    var vm = this
    this.operateUser = this.$jsfn.determineUserLoginSuccess() // 'src/assets/js/jsfn.js'
    if (this.operateUser) {
      console.log('User')
      // assistantmyBoss
      if (this.operateUser.identity === 'assistant') {
        console.log('')
        switch (this.operateUser.chooseStoreOwner) { // -1()01()()
          case -1:
            console.log('')
            this.showChooseMyBoss = true // 
            this.$router.push({name: 'ChooseMyBoss'}) // 
            break
          case 0:
            console.log('')
            break
          case 1:
            console.log('')
            break
        }
      }
    } else {
      console.log('User', this.operateUser)
      this.$message({
        type: 'warning',
        message: ''
      })
      vm.$windowFn.openLoginWindow('body', {}, function () { vm.$router.push('/Home') }, function () { vm.$router.push('/User') })
    }
  }
}
</script>

<style scoped>

</style>



