        _id: '' // _id                   [_id]
      }
    }
  },
  created () {
    var cvm = this
    // 
    cvm.getTitleMsg()
    cvm.goodsId = cvm.$route.query._id
    cvm.searchData._id = cvm.$route.query._id
    // this.originalProductData = this.$route.params.data // 
    cvm.getProductByGoodsId() // id
  },
  computed: {
    updateValue () { //  
      return function (originData, newData) {
        return originData === newData ? true : newData
      }
    },
    updateUpdateItemValue () { // 
      var vm = this
      return function () {
        return {
          updateItem: {
            productId: this.updateValue(vm.originalProductData.productId, vm.productId),
            productName: this.updateValue(vm.originalProductData.productName, vm.productName),
            productSeller: this.updateValue(vm.originalProductData.productSeller, vm.productSeller),
            productImage: this.originalProductData.productImage.toString() === this.successBase64Image.toString() ? true : this.successBase64Image,
            productDetails: this.updateValue(vm.originalProductData.productDetails, vm.productDetails),
            productColor: this.updateValue(vm.originalProductData.productColor, vm.productColor),
            productGenre: this.updateValue(vm.originalProductData.productGenre, vm.productGenre),
            productSpecifications: JSON.stringify(this.originalProductData.productSpecifications) === JSON.stringify(this.productSpecifications) ? true : this.productSpecifications,
            productOriginalPrice: this.updateValue(vm.originalProductData.productOriginalPrice, vm.productOriginalPrice),
            productBatchPrice: this.updateValue(vm.originalProductData.productBatchPrice, vm.productBatchPrice),
            productSoloPrice: this.updateValue(vm.originalProductData.productSoloPrice, vm.productSoloPrice),
            productExplain: this.updateValue(vm.originalProductData.productExplain, vm.productExplain)
          }
        }
      }
    },
    allNewProductData () {
      // this.productId
      // this.productName
      // console.log(this)
      // var res = 123
      // console.log(this.productId, this.productName)
      return Date.now()
    }
  },
  watch: {
    '$route' (to, from) { // 
      // console.log(to, from)
      var wvm = this
      wvm.getTitleMsg()
      wvm.goodsId = wvm.$route.query._id // _id
    },
    allNewProductData (newData, oldData) {
      console.log('')
      console.log(newData, '', oldData)
    },
    // 
    'productId' () { // id
      this.updateItem = this.updateUpdateItemValue().updateItem // 
    },
    'productName' () { // 
      this.updateItem = this.updateUpdateItemValue().updateItem // 
    },
    'productSeller' () { // 
      this.updateItem = this.updateUpdateItemValue().updateItem // 
    },
    'successBase64Image' () {
      this.updateItem = this.updateUpdateItemValue().updateItem // 
    },
    'productDetails' () { // 
      this.updateItem = this.updateUpdateItemValue().updateItem // 
    },
    'productColor' () { // 
      this.updateItem = this.updateUpdateItemValue().updateItem // 
    },
    'productGenre' () { // 
      this.updateItem = this.updateUpdateItemValue().updateItem // 
    },
    // ------------------------------------------------------------------------
    'productSpecifications': {
      handler (newData, oldData) {
        this.updateItem = this.updateUpdateItemValue().updateItem // 
      },
      deep: true
    },
    'productOriginalPrice' () { // 
      this.productOriginalPrice = Number(this.productOriginalPrice)
      this.updateItem = this.updateUpdateItemValue().updateItem // 
    },
    'productBatchPrice' () { // 
      this.productBatchPrice = Number(this.productBatchPrice)
      this.updateItem = this.updateUpdateItemValue().updateItem // 
    },
    'productSoloPrice' () { // 
      this.productSoloPrice = Number(this.productSoloPrice)
      this.updateItem = this.updateUpdateItemValue().updateItem // 
    },
    'productExplain' () { // 
      this.updateItem = this.updateUpdateItemValue().updateItem // 
    },

    //  vs 
    'ruleSizes' (newData, oldData) { // 
      var wvm = this
      console.log('', newData, '', oldData, wvm.ruleSizes)
      wvm.distributeRule(wvm.ruleSizes, wvm.productSum)
      // console.log(wvm.productSpecifications.length = Math.ceil(wvm.ruleSizes.length / 2) + 1)
    }
    // ---------------------------------------------------------------
    // 'productSpecifications': {
    //   handler (newData, oldData) {
    //     // console.log('', newData, '', oldData)
    //     this.countProductSize()
    //     this.countProductSum()
    //   },
    //   deep: true
    // }
  },
  mounted () {
    // this.$windowFn.allWindow('AddNewGoods', 'warning', '', '') //  
  },
  methods: {
    /*
    * 
    * */
    // 
    getTitleMsg () {
      // 
      this.$sendRequest.RTSGet('/rm_goods/find_title', {userId: 'id'}).then(res => {
        // console.log('')
        if (res.data.code === 0) {
          // console.log('!')
          this.serverColorData = res.data.data.colorData
          this.serverGenreData = res.data.data.genreData
        } else if (res.code === 1) {
          console.log('!')
        } else {
          console.log('')
        }
      })
    },

    // _id
    getProductByGoodsId () {
      var vm = this
      if (this.searchData._id !== 'add') {
        this.$sendRequest.RTSPost('/rm_goods/goods_by_searchData', {userId: 'Id', searchData: this.searchData}).then(res => {
          if (res.data.code === 0) {
            this.originalProductData = res.data.data
            // console.log(this.originalProductData)
            vm.updateItem = vm.updateUpdateItemValue().updateItem // 
          }
        })
      } else {
        console.log('')
      }
    },

    /*
    * 
    * */
    /* --------------------------- */
    // 
    goAnchor (selector) {
      this.$el.querySelector(selector).scrollIntoView() // querySelectorcssscrollIntoViewtruefalse
    },

    // productIdproductName
    emptyShow () {
      console.log('')
      if (this.productId !== '') { // 
        if (this.productName !== '') { // 
          this.mustFilled = true // 
        } else {
          // console.log('productName')
          this.mustFilled = 'name'
          // window.location.hash = '#addProductName' // url
          this.goAnchor('#addProductName') // 
        }
      } else {
        // console.log('productId')
        this.mustFilled = 'id'
        // window.location.hash = '#addProductId' // Id
        this.goAnchor('#addProductId')// 
      }
    },

    // 
    productMsgBox () {
      console.log('productData')
      var nameSpell = this.$jsfn.fnChangeSpell(this.productName) // 
      // productData
      this.productData.productId = this.productId // Id
      this.productData.productName = this.productName // 
      this.productData.productNameAddId = this.productName + this.productId // +id
      this.productData.productNameFirstSpell = nameSpell[1] // 
      this.productData.productNameFullSpell = nameSpell[0] // 
      this.productData.belongStore = this.belongStore // 
      this.productData.productSeller = this.productSeller // 
      this.productData.productImage = this.successBase64Image // base64
      this.productData.productDetails = this.productDetails // 
      this.productData.productColor = this.productColor // 
      this.productData.productSpecifications = this.productSpecifications // 
      this.productData.productGenre = this.productGenre // 
      this.productData.productOriginalPrice = this.productOriginalPrice // 
      this.productData.productBatchPrice = this.productBatchPrice // 
      this.productData.productSoloPrice = this.productSoloPrice // 
      this.productData.userId = this.userId // 
      this.productData.productExplain = this.productExplain // 
      this.productData.exist = this.exist // 
    },

    /* --------------------------- */
    // 
    uploadImage (event) {
      console.log('')
      let file = event.target.files[0]
      let reader = new FileReader()
      // 
      reader.readAsDataURL(file) // 
      reader.onloadend = (e) => { // base64
        console.log('base64')
        if (this.successBase64Image.indexOf(reader.result) === -1) {
          console.log('')
          this.successBase64Image.push(reader.result)
          // this.successBase64Image[this.uploadNum - 1] = reader.result
          this.uploadNum++
        } else {
          console.log('')
          this.$windowFn.allWindow('AddNewGoods', 'remind', '', {agree: '', cancel: ''}) // 
        }
      }
      // var res = this.$sendRequest.RTSPost('/rm_upload_image/upload', '123')
      // console.log(res);

      // pushsuccessImage
      // this.successBase64Image.push(file)
      console.log('this.successBase64Image', this.successBase64Image, typeof this.successBase64Image, this.originalProductData.productImage)
      event.target.value = '' // value
    },

    // 
    distributeRule (sizes, sum) {
      console.log('')
      this.productSpecifications = [ {size: '0', number: 0} ] // 
      var sizeArray = sizes.split(/,|/) // []
      var sizeArrayNonEmpty = [] // sizeArray
      sizeArray.map(function (e) {
        if (e !== '' && e !== undefined) {
          sizeArrayNonEmpty.push(e)
        }
      })
      var eachSizeNumber = parseInt(sum / sizeArrayNonEmpty.length) // 
      sizeArrayNonEmpty.forEach((e, index) => { // productSpecifications
        this.productSpecifications[index].size = e
        // if (this.productSpecifications[index].size === '') {
        //   console.log('')
        //   this.productSpecifications.splice(index, 1)
        //   // this.productSpecifications[index].number = 0
        // } else {
        //   console.log('')
        this.productSpecifications[index].number = eachSizeNumber
        // }
        if (this.productSpecifications.length < sizeArrayNonEmpty.length) {
          this.addSpecifications()
        }
      })
      if (sum % sizeArrayNonEmpty.length !== 0) { // or
        // 
        var differNumber = sum - eachSizeNumber * sizeArrayNonEmpty.length
        this.productSpecifications[0].number = eachSizeNumber + differNumber
      }
      console.log(this.productSpecifications, typeof this.originalProductData.productSpecifications)
    },

    // 
    countProductSize () {
      console.log('')
      this.ruleSizes = '' // 
      this.productSpecifications.forEach((e, index) => {
        if (index < this.productSpecifications.length - 1) {
          this.ruleSizes += e.size + ','
        } else {
          this.ruleSizes += e.size
        }
      })
    },

    // 
    countProductSum () {
      console.log('', this.productSum)
      this.productSum = 0 // 
      this.productSum = Number(this.productSum) // 
      this.productSpecifications.forEach(e => {
        e.number = Number(e.number)
        console.log(e.number, typeof e.number, typeof this.productSum)
        this.productSum += e.number
      })
    },

    // ({:'', :''})
    addSpecifications () {
      // console.log('')
      this.productSpecifications.push({size: '0', number: 0})
    },

    // 
    deleteSpecifications (ruleIndex) {
      // console.log('' + ruleIndex + '')
      this.productSpecifications.splice(ruleIndex, 1)
      this.countProductSize() // 
      this.countProductSum() // 
    },

    // 
    addGoods () {
      console.log('')
      this.emptyShow() // 
      if (this.mustFilled) {
        this.productMsgBox() // productData
        // console.log(this.productData)
        // 
        this.$sendRequest.RTSPost('/rm_goods/add_goods', {productData: this.productData, userId: this.userId}).then(res => {
          if (res.data.code === 0) {
            // console.log('');
            this.uploadSuccessImage = res.data.image
            this.$windowFn.allWindow( // 
              'AddNewGoods',
              'congratulations',
              {
                text: '' + this.productName + this.productId + '',
                image: res.data.image
              },
              {agree: '', cancel: ''},
              true
            ).then(e => {
              if (e.code === 0) {
                console.log('')
                this.$router.push({name: 'GoodsList'}) // GoodsList.vue
              } else if (e.code === 1) {
                console.log('')
                // 
                this.productId = '' // ()
                this.productName = '' // 
                this.productSeller = '' // 
                this.successBase64Image = [] // 
                this.productDetails = '' // 
                this.productColor = '' // 
                this.productSpecifications = [ // 
                  {size: '', number: 0}
                ]
                this.productGenre = '' // 
                this.productOriginalPrice = 0 // 
                this.productBatchPrice = 0 // 
                this.productSoloPrice = 0 // 
                this.productExplain = '' // 
              }
            })
          } else {
            console.log('')
          }
        })
      } else {
        // this.emptyShow() //  mustFilled = true
      }
    },

    // (successBase64Image)
    deleteImage (index) {
      // 
      // console.log('AddNewGoods.vue')
      this.$windowFn.allWindow('AddNewGoods', 'warning', '', {agree: '', cancel: ''}, true).then((e) => {
        console.log('', e)
        if (e.code === 0) {
          this.successBase64Image.splice(index, 1)
        }
      })
      // console.log('')
    },

    // 
    addition (addendArray) {
      var num = 0
      if (typeof addendArray === 'object') {
        addendArray.forEach(e => {
          if (typeof e.number === 'number') {
            num += e.number
          } else {
            num = ''
          }
        })
      } else {
        console.log('')
      }
      return num
    },

    // 
    ruleAddition (ruleArray) {
      var origRuleSizes = ''
      ruleArray.forEach((e, index) => {
        if (index < ruleArray.length - 1) {
          origRuleSizes += e.size + ','
        } else {
          origRuleSizes += e.size
        }
      })
      return origRuleSizes
    },

    // 
    submitUpdateGoods () {
      console.log('')
      console.log(this.updateItem)
      this.emptyShow()
      if (this.mustFilled === true) {
        var updateData = {} // 
        for (var obj in this.updateItem) { // 
          if (this.updateItem[obj] !== true) {
            updateData[obj] = this.updateItem[obj]
            if (obj === 'productName') {
              var nameSpell = this.$jsfn.fnChangeSpell(this.productName) // 
              updateData.productNameFirstSpell = nameSpell[1] // 
              updateData.productNameFullSpell = nameSpell[0] // 
              updateData.productNameAddId = this.productName + this.productId // +id
            }
            if (obj === 'productId') {
              updateData.productNameAddId = this.productName + this.productId // +id
            }
          }
        }
        console.log(updateData)
        // 
        this.$sendRequest.RTSPost('/rm_goods/update_goods', {updateData: updateData, _id: this.searchData._id, userId: this.userId}).then(res => {
          if (res.data.code === 0) {
            // console.log('');
            this.uploadSuccessImage = res.data.image
            this.$windowFn.allWindow( // 
              'AddNewGoods',
              'congratulations',
              {
                text: '' + this.productName + this.productId + '',
                image: res.data.image
              },
              {agree: '', cancel: ''},
              true
            ).then(e => {
              if (e.code === 0) {
                console.log('')
                this.$router.push({name: 'GoodsList'}) // GoodsList.vue
              } else if (e.code === 1) {
                console.log('')
                // 
                this.getProductByGoodsId() // id
                // 
                this.productId = '' // ()
                this.productName = '' // 
                this.productSeller = '' // 
                this.successBase64Image = [] // 
                this.productDetails = '' // 
                this.productColor = '' // 
                this.productSpecifications = [ // 
                  {size: '', number: 0}
                ]
                this.productGenre = '' // 
                this.productOriginalPrice = 0 // 
                this.productBatchPrice = 0 // 
                this.productSoloPrice = 0 // 
                this.productExplain = '' // 
              }
            })
          } else {
            console.log('')
          }
        })
      } else {
        console.log('')
        // this.emptyShow() //  mustFilled = true
      }
    },
    /*
    * 
    * */
    // 
    regexp (data) {
      console.log('--------------------------------------------------------------')
      var reg = new RegExp('^(?=.*[0-9])(?=.*[A-Za-z])(?=.*[~!@#%^&*\\$\\-\\_+=;:\'"\\{\\}<>,.?/|\\[\\]()\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\`\\\\\\\\\\\\\\\\]).{8,16}$') //  8-16
      // var reg = new RegExp('^(?=.*[0-9])(?=.*[A-Za-z])(?=.*[/\\()-,;:\'!@#%&/*/?/|/_/+/{/}/[/]/^/$/.//]).{8,16}$') //  8-16
      if (reg.test(this.productId)) {
        console.log(reg.test(this.productId))
        console.log('')
      } else {
        console.log(reg.test(this.productId))
        console.log('')
      }
      console.log(data)
    },
    send () {
      this.$sendRequest.RTSGet('/image', '123').then(res => {
        this.picture = res.data.image
        console.log(this.picture)
      })
    },
    testUploadImage (event) {
      let file = event.target.files[0]
      console.log(event)
      console.log(event.target.files[0].size / 1024 / 1024)
      let reader = new FileReader()
      // var xhr = new XMLHttpRequest()
      // 
      reader.readAsDataURL(file) // 
      reader.onloadend = (e) => { // base64
        console.log('base64')
        if (this.successBase64Image.indexOf(reader.result) === -1) {
          console.log('')
          this.successBase64Image.push(reader.result)
          // this.successBase64Image[this.uploadNum - 1] = reader.result
          this.uploadNum++
        } else {
          console.log('')
          this.$windowFn.allWindow('AddNewGoods', 'remind', '', {agree: '', cancel: ''}) // 
        }
        event.target.value = '' // value
      }
    },
    concatTest () {
      // var a = [1, 2, 3]
      // var b = a
      // a[0] = 0
      // console.log(a, b)
      // ---------------------------------------------------------
      console.log(this.productSpecifications, this.originalProductData.productSpecifications)
      this.productSpecifications = JSON.parse(JSON.stringify(this.originalProductData.productSpecifications))

      this.productSpecifications[1].number = 2
      console.log(this.productSpecifications, this.originalProductData.productSpecifications)
      console.log(this.productSpecifications)

      var deepCopy = function (obj) {
        if (typeof obj !== 'object') return
        var newObj = obj instanceof Array ? [] : {}
        for (var key in obj) {
          console.log('', key, obj)
          if (obj.hasOwnProperty(key)) {
            newObj[key] = typeof obj[key] === 'object' ? deepCopy(obj[key]) : obj[key]
            console.log('', key, obj)
          } else {
            console.log('', key, obj)
          }
        }
        return newObj
      }
      var a = [1, {x: 25, y: {z: 23, w: 28}}, 3]
      var b = this.$jsfn.fnArrayDeepCopy(a)
      b[1].y.z = 78
      console.log('b', a, b)
    }
  }
}
</script>

