import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex) // Vuex
// 
const state = {
  user: {
    userId: '', // _id
    userName: '', // 
    identity: '', // 
    myBoss: '', // 
    chooseStoreOwner: '' // 0-1()01()()
  }
}
const mutations = {
// mutationsstatestate
//  mutations 
  updateUserInfo (state, user) { // user
    state.user = user
    console.log('vuexuser', user)
  }
}
// vuex
export default new Vuex.Store({ // vuexstore
  state,
  mutations
})



