<template>
    <div id="UsersList">
      <p class="AppAllExplain">{{msg}}</p>
      <!--  -->
      <div>
        <ul style="" @click="getUsersListBySearch">
          <li style="float: left; border: 1px solid black; border-left: 0; cursor: pointer; padding: 5px;" :class="{headerSearchActive: searchData.identity === ''}" @click="searchData.identity = ''"></li>
          <li style="float: left; border: 1px solid black; border-left: 0; cursor: pointer; padding: 5px;" :class="{headerSearchActive: searchData.identity === identity}" v-for="(identity, index) in identityList" :key="index" @click="searchData.identity = identity">{{identity | EnglishTranslate}}</li>
        </ul>
        <table>
          <thead>
            <tr>
              <th><input type="checkbox" v-on:change="checkAll($event)" :checked="$jsfn.contrastArray(DBIdArray, allIdArray)"></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th>boos</th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(user, index) in usersList" :key="index">
              <td><input type="checkbox" v-on:change="arrangeDBIdArray($event, user._id)" :checked="DBIdArray.indexOf(user._id) === -1? false : true"></td>
              <td>{{index + 1}}</td>
              <td>{{user.userName}}</td>
              <td>{{user.userPassword}}</td>
              <td>{{user.userSex}}</td>
              <td>{{user.telephone}}</td>
              <td>{{user.identity | EnglishTranslate}}</td>
              <td>{{user.shoppingTrolley}}</td>
              <td>{{user.addressList}}</td>
              <td>{{user.lastLoginTime | countDateChinese}}</td>
              <td>
                <ul>
                  <li v-for="(assistant, index) in user.myAssistant" :key="index">{{assistant}}</li>
                </ul>
              </td>
              <td>{{user.myBoss}}</td>
              <td>{{user.meta.createAt}}</td>
              <td>{{user.meta.updateAt | countDateChinese}}</td>
              <td>
                <button @click="deleteOneUserMsg(user._id)"></button>
                <button @click="resetOneUserPassword(user._id)"></button>
              </td>
            </tr>
          </tbody>
        </table>
        <div>
          <button @click="batchDelete"></button>
          <button @click="invertSelection"></button>
        </div>
      </div>
    </div>
</template>

<script>
export default {
  name: 'users-list',
  data () {
    return {
      msg: '',
      usersList: [], // 
      identityList: [ // 
        'onlineBuyer',
        'boss',
        'assistant',
        'admin'
      ],
      searchData: { // 
        identity: ''
      },
      DBIdArray: [], // _id()
      allIdArray: [], // _id
      allItem: false // 
    }
  },
  created () {
    this.getDataList()
  },
  methods: {
    /*
    * 
    * */
    // 
    getDataList () {
      // exist=trueadmin
      this.$sendRequest.RTSGet('/rm_users/complete_users', {operateUser: 'id'}).then(res => {
        // console.log('')
        if (res.data.code === 0) {
          // console.log('')
          console.log(res)
          this.usersList = res.data.data
          this.usersList.forEach(e => { // _id
            this.allIdArray.push(e._id)
          })
        } else if (res.data.code === 1) {
          // console.log('')
          this.$windowFn.allWindow('GoodsList', 'remind', res.data.msg + '', {agree: '', cancel: ''}) // 
        }
      })
    },

    // 
    getUsersListBySearch () {
      console.log('', this.searchData)
      this.$sendRequest.RTSPost('/rm_users/users_by_searchData', {userId: 'id', searchData: this.searchData}).then(res => {
        // console.log('')
        if (res.data.code === 0) {
          // console.log('')
          console.log(res)
          this.usersList = res.data.data
        } else if (res.data.code === 1) {
          // console.log('')
          this.$windowFn.allWindow('GoodsList', 'remind', res.data.msg + '', {agree: '', cancel: ''}) // 
        }
      })
    },
    /*
    * 
    * */
    /* --------------------------- */

    /* ------------------------------- */
    // 
    deleteOneUserMsg (userId) {
      console.log('', userId)
      this.$sendRequest.RTSDelete('/rm_users//delete_users', {operateUser: '', userId: userId}).then(res => {
        var status
        if (res.data.code === 0) {
          status = 'success'
          this.getDataList() // 
        } else {
          status = 'warning'
        }
        this.$message({
          message: res.data.msg,
          type: status
        })
      })
    },

    // 
    checkAll (event) {
      // console.log('', event.target.checked)
      if (event.target.checked) { // 
        this.DBIdArray = this.allIdArray.concat() // _id
      } else {
        this.DBIdArray = [] // 
      }
    },

    // _id
    arrangeDBIdArray (event, DBId) {
      if (event.target.checked) { // 
        // console.log('')
        if (this.DBIdArray.indexOf(DBId) === -1) { // 
          this.DBIdArray.push(DBId) // 
        } else {
          console.log('_id')
        }
      } else {
        console.log('')
        while (this.DBIdArray.indexOf(DBId) !== -1) { // 
          console.log('while')
          this.DBIdArray.splice(this.DBIdArray.indexOf(DBId), 1) // 
        }
      }
    },

    // 
    batchDelete () {
      console.log('', this.DBIdArray)
      this.$sendRequest.RTSDelete('/rm_users/delete_users_batch', {operateUser: '', idArray: this.DBIdArray}).then(res => {
        var status
        if (res.data.code === 0) {
          console.log('')
          status = 'success'
          this.getDataList() // 
        } else if (res.data.code === 1) {
          console.log('')
          status = 'warning'
          this.$windowFn.allWindow('PurchasesList', 'remind', res.data.msg + '', {agree: '', cancel: ''}) // 
        }
        this.$message({
          message: res.data.msg + res.data.data.n + '',
          type: status
        })
      })
    },

    // 
    invertSelection () {
      console.log('')
      var idArray = this.DBIdArray.concat()
      this.DBIdArray = [] // 
      this.usersList.forEach(e => { // _id push_id
        if (idArray.indexOf(e._id) === -1) {
          this.DBIdArray.push(e._id)
        }
      })
    },

    // 
    resetOneUserPassword (userId) {
      console.log('', userId)
      var updateData = {userPassword: '000000'}
      this.$sendRequest.RTSPost('/rm_users/update_users', {operateUser: '', _id: userId, updateData: updateData}).then(res => {
        console.log(res)
        var status, msg
        if (res.data.code === 0) {
          status = 'success'
          msg = ''
          this.getDataList() // 
        } else {
          status = 'warning'
          msg = ''
        }
        this.$message({
          message: msg,
          type: status
        })
      })
    }
  }
}
</script>

<style scoped lang="less">
  #UsersList{
    .headerSearchActive{ /*  */
      background: #df5000;
      color: #d6ff00;
    }
  }
</style>



